* {
  outline: none;
}

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.main {
  margin-left: 30%;
}

.post {
  padding: 10px;

  width: 200px;
}

.ava-img {
  background-color: blue;
  width: 30px;
  height: 30px;
  border-radius: 50%;
  background-size: cover;
}

.size-xl {
  font-size: x-large;
  color: rgb(0, 0, 0);
}

.author-name {
  padding-left: 10px;
  padding-bottom: 10px;
  font-weight: 700;
  font-size: medium;
}

.content {
  width: 300px;
  height: 300px;
  font-size: small;
  margin-left: -150px;
}

.img-content {
  width: 400px;
  height: 400px;
  padding-top: 5px;
