  margin-left: 30px;
  padding-top: 15px;
  width: 380px;
}

.social-buttons i {

  margin-right: 10px;
}

.social-buttons i:last-child {
  margin-right: 0;
}

.fl {
  float: right;
}

.likes {
  font-size: x-small;
  font-weight: 700;
  background-image: url('/public/img-content/22.jpg');
}

