  width: 450px;
}

.text-content {
  margin-top: 5px;
  background-size: contain;
  font-size: small;
}

.comment-text {
  font-size: x-small;

}

.comment-author {
  font-size: small;
  font-weight: 600;
}

.description {
  margin-left: 80px;
