}

.footer-buttons {
  border-top: 1px solid rgb(172, 172, 172);
  position: absolute;
  width: 400px;
  bottom: 0;
  background-color: white;
  position: fixed;
  display: flex;
  justify-content: space-around;
}

.footer-buttons i {
  padding-top: 5px;
}

.head-icons {
